//audio player
.audio-player {
  width: 100%;
  background: #f3f3f3;
  overflow: hidden;
  padding: 12px;
  box-sizing: border-box;
  border-radius: 10px;
  margin: 0 0 32px;
  font-size: 14px;
  color: #595959;
  display: flex;
  align-items: center;

  @media all and (max-width: 780px) {
    padding: 16px 12px;
  }

  .progress-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    margin: 0 8px 0 12px;

    @media all and (max-width: 540px) {
      margin: 0 4px 0 12px;

    }
  }

  .toggle-play {
    width: 65px;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f1563c;
    border-radius: 50%;
    flex-shrink: 0;
    cursor: pointer;

    @media all and (max-width: 780px) {
      width: 60px;
      height: 60px;
    }

    @media all and (max-width: 540px) {
      width: 50px;
      height: 50px;
    }
  }

  .play-pause-img {
    width: 30%;
  }

  .play {
    .play-pause-img {
      margin-left: 3px;
    }
  }

  .pause {
    .play-pause-img {
      margin-left: 0;
    }
  }

  .name {
    padding: 2px;
  }

  .timeline {
    background: white;
    width: 100%;
    position: relative;
    cursor: pointer;
    height: 9px;
    margin: 3px 0;
    border-radius: 50px;
    overflow: hidden;

    .progress {
      background: rgba(241, 86, 60, .8);
      width: 0%;
      height: 100%;
      transition: 0.25s;
    }
  }

  .controls {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .time {
      display: flex;

      > * {
        padding: 2px;
      }
    }
  }

  .volume-container {
    cursor: pointer;
    height: 26px;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    padding: 5px 3px 5px 0;
    border-radius: 50px;
    margin-left: 0;
  }

  .volume-button {
    height: 26px;
    display: flex;
    align-items: center;
    margin-left: 4px;
  }

  .volume {
    transform: scale(0.7);
  }

  .volume-slider {
    //position: absolute;
    //position: static;
    //left: -3px;
    //top: 5px;
    //z-index: -1;
    width: 0;
    height: 9px;
    background: white;
    transition: .25s;
    border-radius: 50px;
    overflow: hidden;

    .volume-percentage {
      background: #f1563c;
      height: 100%;
      width: 100%;
    }
  }
}

.no-touch-device {
  .audio-player {
    .volume-container {
      &:hover {
        background-color: #e5e5e5;
        padding: 5px 3px 5px 13px;
        margin-left: 10px;

        .volume-slider {
          //left: -123px;
          width: 120px;
        }
      }
    }
  }
}
