.first-load {
  .post-item:first-of-type {

    @media all and (min-width: 541px) {
      width: 100%;
      position: relative;
      padding: 17px 16px 32px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;
      box-sizing: border-box;
      color: #FFFFFF;
      margin: 0 0 40px;
      overflow: hidden;
      min-height: 340px;
      margin: 0 10px 40px;
    }

    @media all and (min-width: 680px) {
      padding: 40px;
      min-height: 350px;
    }

    @media all and (min-width: 768px) {
      margin: 0 15px 40px;
    }

    @media all and (min-width: 992px) {
      min-height: 450px;
    }

    @media all and (min-width: 541px) {

      &:hover {
        .post-item__title {
          color: #ffffff;
        }
      }
    }


    &:after {
      @media all and (min-width: 541px) {
        content: '';
        display: block;
        position: absolute;
        background: rgba(0, 0, 0, 0.6);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }

    .post-item__link {
      @media all and (min-width: 541px) {
        left: 0;
        right: 0;
      }
    }

    .post-item__image-wrap {
      @media all and (min-width: 541px) {
        margin: 0;
      }
    }

    .post-item__image {
      @media all and (min-width: 541px) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 100%;
        z-index: -2;
      }
    }

    .post-item__text-wrap {
      @media all and (min-width: 541px) {
        margin-top: 70px;
        margin-bottom: 16px;
        position: relative;
        z-index: 1;
      }

      @media all and (min-width: 680px) {
        margin-top: 50px;
        margin-bottom: 32px;
      }

      @media all and (min-width: 992px) {
        margin-top: 70px;
      }
    }

    .post-item__meta-info {
      @media all and (min-width: 541px) {
        margin-bottom: 0;
        font-size: 14px;
        color: #FFFFFF;
        position: absolute;
        top: 17px;
        left: 16px;
        z-index: 3;
      }

      @media all and (min-width: 680px) {
        top: 40px;
        left: 40px;
      }
    }

    .post-item__meta-info-item {
      &:first-of-type {
        &:after {
          @media all and (min-width: 541px) {
            top: 2px;
          }

          @media all and (min-width: 768px) {
            top: 6px;
          }
        }
      }
    }

    .post-item__permalink {
      @media all and (min-width: 541px) {
        color: inherit;
      }
    }

    .post-item__title {
      @media all and (min-width: 541px) {
        margin-bottom: 8px;
        color: inherit;
        transition: .3s;
      }

      @media all and (min-width: 680px) {
        margin-bottom: 16px;
      }

      @media all and (min-width: 768px) {
        font-size: 36px;
        line-height: 52px;
      }

      @media all and (min-width: 992px) {
        font-size: 40px;
      }

      @media (hover: hover) {
        &:hover {
          color: #f1563c;
        }
      }
      //&:hover {
      //  color: #f1563c;
      //}
    }

    .post-item__description {
      @media all and (min-width: 768px) {
        font-size: 18px;
        line-height: 24px;
      }
      @media all and (min-width: 540px) and (max-width: 992px) {
        display: none;
      }
    }

    .post-item__read-link {
      @media all and (min-width: 541px) {
        display: flex;
        margin-top: 0;
      }
    }
  }
}

