//blog filter
.industries__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  opacity: 1;
  visibility: visible;
  transition: .4s;
  position: relative;
  transform: translateY(0);
  margin-bottom: -12px;

  &.hide {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    transform: translateY(-20px);
  }
}

.industries__item {
  margin: 0 16px 12px 0;

  @media all and (max-width: 580px) {
    margin: 0 15px 12px 0;
  }

  @media all and (max-width: 370px) {
    margin: 0 10px 12px 0;
  }

  &-inner {
    font-size: 14px;
    line-height: 24px;
    color: #666666;
    padding: 3px 12px;
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
    border-radius: 20px;
    transition: .4s;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;

    @media all and (max-width: 370px) {
      padding: 2px 10px;
      font-size: 12px;
    }

    @media (hover: hover) and (pointer: fine) {
      &:hover {
        border: 1px solid #F1563C;
        color: #F1563C;
      }
    }

    &.category-active {
      border: 1px solid #F1563C;
      color: #F1563C;

      .close-btn-wrap {
        width: 20px;
        opacity: 1;
        visibility: visible;
        padding-left: 4px;
      }

      .close-btn {
        width: 14px;
      }
    }
  }

  .close-btn-wrap {
    display: flex;
    width: 0;
    opacity: 0;
    visibility: hidden;
    padding-left: 0;
    box-sizing: border-box;
    transition: .4s;
  }

  .close-btn {
    display: inline-block;
    position: relative;
    width: 0;
    height: 14px;
    transition: .2s;
    cursor: pointer;

    .close-btn-line {
      display: inline-block;
      width: 15px;
      height: 1px;
      background-color: #f1563c;
      position: absolute;
      top: 50%;
      left: 50%;
      transform-origin: center;

      &.first {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &.second {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }
}

.single-post-wrapper {

  div#ez-toc-container {
    background: #F9F9F9;
    border: 1px solid #F9F9F9;
    padding: 16px 24px;
    margin: 32px 0 40px;
    border-radius: 0;
    box-sizing: border-box;

    @media all and (max-width: 370px) {
      padding: 16px;
    }

    p.ez-toc-title {
      display: block;
      color: #333333;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      margin: 0;
    }

    nav > ul {
      padding-top: 8px;
    }

    ul ul {
      @media all and (max-width: 370px) {
        margin-left: 16px;
      }
    }

    ul li {
      font-size: 14px;
      line-height: 24px;
    }

    a, a:link {
      border-bottom: none;
    }

    a:hover {
      text-decoration: none;
    }

    .ez-toc-title-container {
      display: flex;
    }

    .ez-toc-icon-toggle {
      display: none;
    }

    .ez-toc-icon-toggle:before {
      content: "";
    }

    .ez-toc-btn-default {
      background: none;
      border: none;
      padding: 0;
      width: 24px;
      height: 24px;
      display: flex !important;
      align-items: center;
      justify-content: center;
      line-height: 1;
      padding-top: 1px;
      box-sizing: border-box;

      &:after {
        display: inline-block;
        content: '';
        background: url("../images/blog/arrow-icon.svg") no-repeat center/100%;
        width: 12px;
        height: 7px;
        transition: .2s;
        -webkit-backface-visibility: visible;
        backface-visibility: visible;
        //-webkit-transform: rotate3d(1, 0, 0, 180deg);
        transform: rotate(180deg);
        will-change: transform;
      }

      &.rotate {
        &:after {
          //-webkit-transform: rotate3d(1, 0, 0, 0deg);
          transform: rotate(0);
        }
      }
    }

    .ez-toc-pull-right {
      float: none !important;
      margin-left: 4px;
    }

    .ez-toc-title-toggle {
      @media all and (min-width: 993px) {
        //display: none !important;
      }
    }
  }
}



