.feedback-block{
  font-family: "Roboto", sans-serif;
  height: auto;
  min-height: 34px;
  overflow: auto;
  border-style: solid;
  border-width: 1px 0 1px 0;
  border-color: #ccc;
  padding: 26px 0;
  font-size: 16px;
  line-height: 34px;
  vertical-align: middle;
  display: flex;
  justify-content: space-between;
}
.feedback-action-list {
  display: inline-block;
}
.feedback-block.feedback-done .feedback-info {
  display: none;
}
.feedback-block.feedback-done .feedback-positive {
  display: block;
  width: 100%;
  text-align: center;

}
.left-info span {
  color: #565656;
  padding-right: 5px;
}
.feedback-positive {
  display: none;
}
.feedback-btn {
  display: inline-block;
  text-decoration: none;
  color: #f1563c;
  background: transparent;
  border: 1px solid rgba(239, 73, 52, 0.7);
  font-size: 14px;
  line-height: 32px;
  border-radius: 6px;
  width: auto;
  min-width: 98px;
  text-align: center;
  margin: 0 5px;
  padding: 0;
  -webkit-appearance: none;
  cursor: pointer;
  transition: background-color 0.06s ease;
  -webkit-transition: background-color 0.06s ease;
}
.no-touch .feedback-btn:hover {
  color: #f1563c;
  background: transparent;
  border: 1px solid rgba(239, 73, 52, 1);
}
.feedback-btn.grey-btn {
  background-color: #f1563c;
  text-transform: uppercase;
  border-color: transparent;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 49px;
  height: 49px;
  border-radius: 25px;
  min-width: 146px;
}
.no-touch .feedback-btn.grey-btn.disabled:hover,
.feedback-btn.grey-btn.disabled {
  background-color: #b4b4b4;
  cursor: not-allowed;
  color: #fff;
  border-color: transparent;
}
.feedback-wrapper {
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.16);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.06s ease;
  -webkit-transition: opacity 0.06s ease;
}
.feedback-popup {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  max-width: 800px;
  background: #fff;
  text-align: center;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-sizing: border-box;
  padding: 36px 100px;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.popup-fixed {
  z-index: 9999;
  opacity: 1;
}
.popup-fixed.feedback-wrapper {
  transition: all 0.06s 0s, opacity 0.06s ease;
  -webkit-transition: all 0.06s 0s, opacity 0.06s ease;
}
.feedback-popup .close-btn:link,
.feedback-popup .close-btn:visited,
.feedback-popup .close-btn {
  display: block;
  position: absolute;
  top: 15px;
  right: 15px;
  height: 15px;
  width: 15px;
  border: none;
}
.feedback-popup .close-btn svg {
  height: 15px;
  width: 15px;
  opacity: 0.4;
}
.no-touch .feedback-popup .close-btn:hover svg {
  opacity: 0.8;
}
.feedback-popup textarea {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 300;
  color: #333;
  width: 100%;
  border: none;
  outline: none;
  border-bottom: 1px solid #ccc;
  padding: 25px 0 0;
  margin: 0 auto 30px;
  height: 42px;
  transition: height 0.2s ease;
  -webkit-transition: height 0.2s ease;
}
.feedback-popup textarea.filled,
.feedback-popup textarea:focus {
  height: 88px;
}
.feedback-popup textarea::placeholder {
  font-family: "Roboto", sans-serif;
  color: #a3a3a3;
}
.feedback-popup-text {
  font-size: 18px;
  color: #333;
  display: block;
  font-weight: 300;
}
.feedback-question {
  font-size: 34px;
  font-weight: 700;
}
