.search-overlay {
    display: none;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: fixed;
    opacity: 0;
    top: 160px;
    left: 0;
    overflow: hidden;
    z-index: 85;
    background-color: rgba(255, 255, 255, 1);
}

.search-overlay.menu-opened {
    display: block;
    opacity: 1;
}

.blog-search-overlay .search-form {
    overflow: hidden;
    position: relative;
    margin: 0 auto;
    padding-bottom: 0;
    transition: .4s;
}

.blog-search-overlay .wrapper-search {
    width: auto;
    max-width: 996px;
    margin: 0 auto;
    padding: 85px 20px 20px;
    height: calc(100% - 175px);
}

.blog-search-overlay .search-form {
    height: 100%;
    overflow: hidden;
    position: relative;
}

.blog-search-overlay .search-icon {
    width: 14px;
    height: 15px;
    position: absolute;
    left: 5px;
    top: 7px;
}

.search-overlay .logo-search {
    z-index: 99;
    height: 60px;
    width: 60px;
    position: absolute;
    top: 0;
    left: 0;
}

.blog-search-overlay .search-form input {
    -webkit-appearance: none;
    font-size: 18px;
    line-height: 24px;
    background: none;
    border: none;
    width: 100%;
    padding: 0 0 7px 30px;
    border-bottom: 1px solid #C4C4C4;
    box-sizing: border-box;
    outline: none;
    margin-bottom: 16px;
    border-radius: 0;
}

