    .blog-search-overlay .search-form input {
        margin-bottom: 14px;
    }
}

.blog-search-overlay .search-form input:focus,
.active-search .blog-search-overlay .search-form input {
    border-bottom-color: #F1563C;
}

.blog-search-overlay .search-form input::-webkit-input-placeholder {
    color: #666666;
}

.blog-search-overlay .search-form input::-moz-placeholder {
    color: #666666;
}

.blog-search-overlay .search-form input:-ms-input-placeholder {
    color: #666666;
}

.blog-search-overlay .search-form input:-moz-placeholder {
    color: #666666;
}

.blog-search-overlay .search-form input:-webkit-autofill {
    background: none;
    color: #fff;
}

