    display: none;
}

.search-overlay .search-form .count {
    position: absolute;
    display: none;
    text-align: center;
    color: #a3a3a3;
    font-size: 16px;
    font-family: "Roboto", sans-serif;
    padding: 0;
    background: rgba(255, 255, 255, .8);
    top: 75px;
    width: 200px;
    left: 50%;
    margin: 0 0 0 -100px;
}

.subscribe-content .close-btn,
.blog-search-overlay .close-btn {
    display: inline-block;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.blog-search-overlay .close-btn {
    position: absolute;
    right: 0;
    top: 5px;
    transition: .2s;
    opacity: 0;
    visibility: hidden;
}

.active-search .blog-search-overlay .close-btn {
    opacity: 1;
    visibility: visible;
}

.subscribe-content .close-btn .close-btn-line,
.blog-search-overlay .close-btn .close-btn-line {
    display: inline-block;
    width: 17px;
    height: 1px;
    background-color: #666666;
    position: absolute;
    top: 50%;
    left: 50%;
    transform-origin: center;
}

.close-btn-line.first {
    transform: translate(-50%, -50%) rotate(45deg);
}

.close-btn-line.second {
    transform: translate(-50%, -50%) rotate(-45deg);
}


.search-overlay .search-form button {
    display: none;
}

